import React,{Component} from 'react'
import { connect } from 'react-redux';
import MsgMe from './children/msgMe'
import MsgToMe from './children/msgToMe'
import ToolTip from './children/toolTip'
import '../../css/iphone.css'
class Iphone extends Component {
    constructor(props) {
        super(props)
        this.state = {
            isToolShow:true,
            fromDanHuang:[
                {   id:'0000',
                    left:'',
                    response:[
                        {
                            content:'',
                            nextLeft:'0001'
                        },{
                            content:'',
                            nextLeft:'0002'
                        }
                    ]
                },
                {   id:'0001',
                    left:'',
                    response:[
                        {
                            content:'',
                            nextLeft:'0003'
                        },{
                            content:'',
                            nextLeft:'0004'
                        },{
                            content:'',
                            nextLeft:'0005'
                        }
                    ]
                },
                {   id:'0002',
                    left:'',
                    response:[
                        {
                            content:'',
                            nextLeft:'0006'
                        },{
                            content:'',
                            nextLeft:'0007'
                        }
                    ]
                },
                {   id:'0003',
                    left:'""',
                    response:[
                        {
                            content:'',
                            nextLeft:'0008'
                        }
                    ]
                },
                {   id:'0004',
                    left:'',
                    response:[
                        {
                            content:'',
                            nextLeft:'0009'
                        }
                    ]
                },
                {   id:'0005',
                    left:'carry',
                    response:[
                        {
                            content:'',
                            nextLeft:'0010'
                        }
                    ]
                },
                {   id:'0006',
                    left:'18',
                    response:[
                        {
                            content:'',
                            nextLeft:'0009'
                        }
                    ]
                },
                {   id:'0007',
                    left:'...',
                    response:[
                        {
                            content:'',
                            nextLeft:'0009'
                        }
                    ]
                },
                {   id:'0008',
                    left:'',
                    response:[
                        {
                            content:'',
                            nextLeft:'1000'
                        }
                    ]
                },
                {   id:'0009',
                    left:'...',
                    response:[
                        {
                            content:'',
                            nextLeft:'1000'
                        }
                    ]
                },
                {   id:'0010',
                    left:'',
                    response:[
                        {
                            content:'',
                            nextLeft:'1000'
                        }
                    ]
                },
                {   id:'1000',
                    left:'Wi-Fi',
                    response:[
                        {
                            content:'Wi-Fi',
                            nextLeft:'100',
                        }
                    ]
                },
            ],
            msg:[{left:'',right:''}],
            tooltip:'',
            toDanhuang:'...'

        };
    }
    handleTool=()=>{
        this.setState(preState=>({
            isToolShow:!preState.isToolShow
        }));

    }
    handleNext=(e)=>{
        e.stopPropagation();
        e.preventDefault();
        let next=e.currentTarget.getAttribute('data-key');
        if(next==100){
            this.setState(preState=>({
                isToolShow:!preState.isToolShow
            }));
            return
        }
        let value=e.currentTarget.getAttribute('value');
        let arr=this.state.msg;
        arr[arr.length-1].right=value;
        this.state.fromDanHuang.forEach(item=>{
            if(item.id==next){
                arr.push({left:item.left,right:''});
                this.setState(preState=>({
                    msg:arr,
                    tooltip:item.response,
                    isToolShow:!preState.isToolShow
                }));
            }
        })
    }
    componentDidMount() {
        this.setState(preState=>({
            tooltip:preState.fromDanHuang[0].response,

        }))
    }
    render(){
        return(
            <div className="iphone">
                <header>-</header>
                <div className="content">
                    {
                        this.state.msg.map((item,index) =>
                                <div key={'msgToMe'+index}>
                                    <MsgMe num={this.state.msg.length-1}index={index}msg={item.left}/>
                                    <MsgToMe msg={item.right}/>
                                </div>
                        )
                    }
                </div>
                <footer onClick={this.handleTool}>{this.state.toDanhuang}</footer>
                {this.state.isToolShow?'':(
                    <ToolTip nextMsg={this.handleNext}
                             tooltip={this.state.tooltip}
                             onToolTip={this.handleTool}/>
                )}
            </div>
        )
    }
}
export default Iphone


